.light-link {
    color: #fff;
    &:hover {
        color: #fff;
        text-decoration: none;
        color: darken(#fff, 15%); } }
.light {
    color: #fff; }
.section {
    &-dark-blue {
        padding: 100px 0;
        background-color: $dark-bg-color; }
    &-headline {
        text-align: center;
        &__title {
            @include black-font;
            font-size: 36px;
            margin-bottom: 30px;
            @media (max-width: 768px) {
                font-size: 28px;
                margin-bottom: 20px; } }
        &__subtitle {
            @include reg-font;
            font-size: 20px;
            display: block;
            margin-bottom: 60px;
            @media (max-width: 768px) {
                font-size: 16px;
                margin-bottom: 50px; } } } }
